const ScrollBtn = ({ handleScroll, icon, alt }) => (
  <nav aria-label="page scroll navigation">
    <button
      className="mb-8 bg-gray-200 opacity-80 p-4 hover:opacity-100 hover:bg-gray-100"
      type="button"
      onClick={handleScroll}
    >
      <img src={icon} alt={alt} className="arrow-icon opacity-80 hover:opacity-100" height="35px" width="35px" />
    </button>
  </nav>
);

export default ScrollBtn;
